import userService from '../services/users'
import linkService from '../services/links'

const userReducer = (store = [], action) => {
  switch(action.type) {
    case 'INIT_USERS':
      console.log('Initiate users userReducer')
      return action.data
    case 'ADD_LINK_FOR_USER':
      console.log('ADD_LINK_FOR_USER userReducer')
      let old = []
      let modified = []
      store.forEach(u => {
        if (u.id === action.userId) {
          modified.push(u)
        } else {
          old.push(u)
        }
      })
      /*Nyt siis pelkstn kyttjn favouritelinkkien tila on pivitetty*/
      return [...old, {...modified[0], links: [...modified[0].links, action.data]}]
    default:
      console.log('default in userReducer')
      return store
  }
}

export const usersInitialization = () => {
  console.log('usersInitialization in userReducer')
  /*Eli tuo links pit kokeilla miten toimii, koska ilmeisesti ei pysty laittamaan
  links: [{link1}, {link2}, {link3}, ...]*/
  const formatUser = (user) => {
    return {
      id: user.id,
      username: user.username,
      name: user.name,
      links: user.links
    }
  }

  return async (dispatch) => {
    const users = await userService.getAll()
    let formattedUsers = []
    users.forEach(u => {
      formattedUsers.push(formatUser(u))
    })
    dispatch({
      type: 'INIT_USERS',
      data: formattedUsers
    })
  }
}


/*T saattais olla fiksumpaa sijoittaa linkReduceriin*/
export const addFavouriteForUser = (linkObject, userId) => {
  console.log('addFavouriteForUser userService')
  /*YTSearchResultissa on jo tarkistettu ettei kyttjll ole jo kyseist
  linkki suosikeissa.*/

  return async (dispatch) => {
    const link = await linkService.createAndAddLinkToUserFavourites(linkObject)
    /*Tmn jlkeen tietokantaan on listty kyttjn links kenttn
    tm uusi linkki. Ja linkkitietokantaan on listty kyseinen linkki, jos
    se ei ollut siell aiemmin.*/
    dispatch({
      type: 'ADD_LINK_FOR_USER',
      data: link,
      userId: userId
    })
  }
}

export default userReducer



